@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&family=Poppins:wght@400;500;600;700;800&display=swap);
*{
  margin: 0;
  padding: 0;

}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    margin:0;
    padding: 0;
}
.imageDiv{
    background: url(/Trip--o-manic/static/media/contactus.9560309c.jpg);
   
    background-size: contain;
    background-attachment: fixed;
    
    width: 100%;
    height: 35vh;
}
.lets{
    font-family: 'Kaushan Script', cursive;
    font-size: 6vh;
    z-index: 1;
    position: relative;
    top: 45%;
    font: bolder;

    opacity: .55;
    color: black;
    padding: 0vh;
    margin: auto;
    left: 20%;
}
.trans{
   
    width: 100%;
    height: 35vh;
    background-color: rgba(118, 116, 127, 0.2);
    
    position: absolute;
    top: 0vh;
}

@media(min-width:750px){
    .lets{

        font-size: 6vh;
        font: bolder;
    }
    
}
@media(min-width:1050px){
            .imageDiv{
                height: 40vh;
                background: url("/Trip--o-manic/static/media/pexels-savvas-stavrinos-814544 (1).5363d02b.jpg");
                background-size: cover;
               background-position: 0 40vw;
                background-attachment: fixed;
            }
   .trans{
       height: 40vh;
   }
   .lets{
       font-size: 15vh;
       left: 33%;
       top: 30%;
       color: white;
       opacity: .8;
   }
    
}
*{
    margin:0;
    
    padding: 0;
}
.inputDiv{
    border: black 9px solid;

    background-color: rgb(196,196,196);
    width: 77%;
   
    padding: 12px;
    margin: auto;
    text-align: center;
    position: relative;
    margin-top: 6vh;
    
    font-family: 'Agency FB', cursive;
    
}
.kindly{
    position: relative;
    left: 20%;
    
    font-size: 3vw;
}
.button{
    position: relative;  
    width: 52%;
    height: 7vh;
    background-color: rgb(118,116,127);
    color: rgb(60, 57, 75);
    font-size: 2.5vh;
}
.button:hover{
    background-color:transparent;
    color: rgb(118,116,127);
    border: rgb(60, 57, 75) 3px solid;
}
label {
  
        font-weight: 900;
    
        display: flow-root;
        padding: 0;
        font-size: 150%;
    }
.bodydiv{
     background:radial-gradient(rgb(60, 57, 75),rgb(0,0,0));
     height: 100%;
     margin-top: -6vh;
}
.divisioncontact{
    position: relative;
    padding: 1vh;
    margin: 2vh;
    box-shadow: 2px 6px 10px -1vw grey;

}


.input{
    font-size: 100%;
    position: relative;
    width: 80%;
    height: 5vh;
    background-color: rgb(118,116,127);
    
}
span{
    font-size: 132%;
    color: red;
    position: relative;
    
    padding: 4px;
  
    
}

.inputFocus{
    border: brown 3px solid;
}

@media (min-width:700px){
    .inputDiv{
        width: 70%;
       
    }
    
    .input{
    
        margin: 2%;
    }
    label{
        font-size: 200%;
        
    }
    .divisioncontact{
        box-shadow: 2px 6px 10px -1vw grey;
    }
    
   
}
.h5{
    font-size: 2vh;
 font-family:"agency fb";
 color: white;
}
@media (min-width:1020px){
    .inputDiv{
        width: 60%;
      
    }
